/* Hides refresh buttons if state is not fully loaded */

.refresh-buttons {
  visibility: hidden;
  display: -webkit-flex;
  padding-top: 1.5vh;
}

.loaded .refresh-buttons {
  visibility: visible;
}

.refresh-buttons button {
  padding: 1vh 3vw;
  background-color: #333;
  color: lightgrey;
  border-radius: 100vw;
  font-size: 3.7vw;
}

.refresh-station-button {
  cursor: pointer;
  margin: auto;
}



